<template lang="html">
  <div class="Message">
    <div class="info" v-if="show">{{msg}}</div>
  </div>
</template>

<script>
export default {
  name: 'Message',
  props: {
    active: Number,
    Status: Number
  },
  data () {
    return {
      msg: '',
      show: false
    }
  },
  watch: {
    msg () {
      if (!this.msg) {
        //  msg
        this.show = false
      } else {
        this.show = true
        setTimeout(() => {
          this.show = false
        }, 3000)
        //  3
      }
    },
    active () {
      this.msg = ''
      switch (this.active) {
        case 1:
          this.msg = ''
          break
        case 0:
          this.msg = ''
          this.resetStatus()
          //  
          break
      }
    },
    Status () {
      switch (this.Status) {
        case 0:
          this.msg = ''
          break
        case 1:
          this.resetStatus()
          this.msg = ''
          break
        case 2:
          this.msg = ''
          this.resetStatus()
          break
        case 3:
          this.resetStatus()
          this.msg = ''
          break
      }
    }
  },
  methods: {
    resetStatus () {
      this.$emit('reset')
      // 
    }
  }
}
</script>

<style lang="stylus" scoped>
.info
  position: fixed
  top: 74%
  left: 30%
  right: 30%
  // bottom: 14%
  color: #fff
  background: rgba(80, 187, 167, 0.8)
  text-align: center
  height: .6rem
  line-height: .6rem
  border-radius: .2rem
</style>
